body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  font-family: 'Redressed', cursive;
}

.home {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.playerCharacters, .places {
  display: flex;
  height: auto;
  /*align-items: center;*/
  justify-content: center;
  font-size: 3rem;
}

.navbar {
    background-color: black;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    
  }

  .menu-bars {
      margin-left: 2rem;
      background: none;
      font-size:  30px;
  }

  .nav-menu {
      background-color: black;
      width: 250px;
      height: 100vh;
      display: flex;
      justify-content: center;
      position: fixed;
      top: 0;
      left: -100%;
      transition: 850ms;
  }

  .nav-menu.active {
      left: 0;
      transition: 350ms;
  }

  .nav-text {
      display: flex;
      justify-content: start;
      align-items: center;
      padding: 8px 0px 8px 16px;
      list-style: none;
      height: 60px
  }

  .nav-text a {
      text-decoration: none;
      color: white;
      font-size:  18px;
      width: 95%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 16px;
      border-radius: 4px;
  }

  .nav-text a:hover {
      background-color: aquamarine;
  }

  .nav-menu-items {
      width: 100%;

  }

  .navbar-toggle {
      background-color: black;
      width: 100%;
      height: 80px;
      display: flex;
      justify-content: start;
      align-items: center;
  }

  span {
      margin-left: 16px;
  }
