  @media (max-width: 900px) {
    .lg-pc-hdr { display: none;}
  }

  @media (max-width: 600px) {
  }

  @media (min-width: 600px) {
    .cards { grid-template-columns: repeat(2, 1fr);     }
    .cards-pc { grid-template-columns: repeat(2, 1fr);
    }
  }

  @media (min-width: 900px) {
    .cards { grid-template-columns: repeat(3, 1fr); }
    .cards-pc { grid-template-columns: repeat(3, 1fr);    }
    .card-pc {margin-left: 1vh;}
    .md-pc-hdr { display: none;}


  }

  .lg-pc-hdr{
    height: auto;
  }
  .md-pc-hdr{
    height: auto;
  }

  .cards-pc {
    max-height: 700px;
    max-width: 100%;
    width: 100%;
    margin: 0 0;
    display: grid;
    grid-row-gap: 50px;
    justify-content: center;
    align-items: center;
    text-align: center;

  }

  .card-pc {
    color: black;
    padding: 1rem;
    width: 400px;
    max-width: 400px;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: auto;

  }

  .cards {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-row-gap: 200px;
    justify-content: center;
  }

  .card {
    color: black;
    padding: 1rem;
    height: 300px;
    max-height: 300px;
    width: 300px;
    max-width: 300px;
  }



